import React from 'react';
import styled from 'styled-components';
import { Headline3 } from '@sberdevices/plasma-styles';
import { primary } from '@sberdevices/plasma-tokens';
import { GradientFadeContainer, useFadeScrollProgress } from 'components/GradientFade/GradientFade';

import { useScrollProgress } from './CartList.hooks';

export const CartListContainerPortal = styled.div`
    position: relative;

    height: 100%;
    width: 528px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    justify-content: center;
`;

const StyledScrollerContainer = styled.div`
    --spatial-navigation-contain: contain;

    position: relative;

    max-height: 410px;
    overflow-x: hidden;
    overflow-y: scroll;
`;

const StyledScrollerWrapper = styled.div`
    overflow-x: hidden;

    will-change: transform;
    transition: transform 0.5s ease-in-out;
`;

const scrollSize = 1;

interface CartListScrollerProps {
    itemCount: number;
}

export const CartListScrollerPortal: React.FC<CartListScrollerProps> = ({ children, itemCount }) => {
    const { scrollerRef, handleScroll, scrollProgress, isScrollable } = useScrollProgress({ itemCount, scrollSize });

    const fadeProps = useFadeScrollProgress({ scrollProgress, isScrollable });

    return (
        <GradientFadeContainer fadeSize={[160]} {...fadeProps}>
            <StyledScrollerContainer ref={scrollerRef} onScroll={handleScroll}>
                <StyledScrollerWrapper>{children}</StyledScrollerWrapper>
            </StyledScrollerContainer>
        </GradientFadeContainer>
    );
};

export const CartTotalPricePortal = styled(Headline3)`
    padding-left: 16px;

    color: ${primary};
`;
