import React from 'react';
import styled, { css } from 'styled-components';
import { transition } from 'style/const/transition';

type FadeSide = 'top' | 'bottom' | 'left' | 'right';

type ContainerAxis = 'x' | 'y';

type ContainerFadeSide = 'start' | 'end';
interface GradientFadeProps {
    fadeSize: number;
    side: FadeSide;
    isVisible?: boolean;
}

type IsAxisStraight = boolean;

const mapSideToAxis: Record<FadeSide, [ContainerAxis, IsAxisStraight]> = {
    left: ['x', true],
    right: ['x', false],
    top: ['y', true],
    bottom: ['y', false],
};

const mapAxisToSide: Record<ContainerAxis, Record<ContainerFadeSide, FadeSide>> = {
    x: {
        start: 'left',
        end: 'right',
    },
    y: {
        start: 'top',
        end: 'bottom',
    },
};

const getOffset = (side: FadeSide, isVisible: boolean, fadeSize: number) => {
    const [offsetAxis, isStraight] = mapSideToAxis[side];

    const value = -1 * Number(isVisible !== isStraight) * fadeSize;

    return ['x', 'y'].map((axis) => (axis === offsetAxis ? `${value}px` : 0)).join(' ');
};

export const GradientFade = styled.div<GradientFadeProps>`
    ${({ side, fadeSize, isVisible = false }) => css`
        mask-image: linear-gradient(to ${side}, #000 calc(100% - ${fadeSize}px), transparent 100%);
        mask-size: calc(100% + ${fadeSize}px) calc(100% + ${fadeSize}px);
        mask-position: ${getOffset(side, isVisible, fadeSize)};
    `}

    mask-repeat: no-repeat;
    transition: mask-position ${transition.default}, -webkit-mask-position ${transition.default};
`;

interface GradientContainerProps {
    fadeSize: [number] | [number, number];
    axis?: ContainerAxis;
    start?: boolean;
    end?: boolean;
}

export const GradientFadeContainer: React.FC<GradientContainerProps> = ({
    fadeSize,
    axis = 'y',
    start,
    end,
    children,
}) => {
    const fadeSizeStart = fadeSize[0];
    const fadeSizeEnd = fadeSize[1] ?? fadeSize[0];

    return (
        <GradientFade fadeSize={fadeSizeStart} side={mapAxisToSide[axis].start} isVisible={start}>
            <GradientFade fadeSize={fadeSizeEnd} side={mapAxisToSide[axis].end} isVisible={end}>
                {children}
            </GradientFade>
        </GradientFade>
    );
};

interface UseFadeScrollProgress {
    (params: { scrollProgress: number; isScrollable?: boolean }): Record<ContainerFadeSide, boolean>;
}

export const useFadeScrollProgress: UseFadeScrollProgress = ({ scrollProgress, isScrollable = true }) => ({
    start: isScrollable && scrollProgress > 0,
    end: isScrollable && scrollProgress < 1,
});
