import React from 'react';
import styled from 'styled-components';
import { Footnote1 } from '@sberdevices/plasma-styles';
import { warning } from '@sberdevices/plasma-tokens';
import { useSelector } from 'hooks/useSelector';
import {
    CartListContainerPortal,
    CartListScrollerPortal,
    CartTotalPricePortal,
} from 'components/Cart/List/CartList@portal';

import { CartListItemPortal } from '../ListItem/CartListItem@portal';

const StyledMinimalPrice = styled(Footnote1)`
    margin-top: 8px;

    padding-left: 16px;

    color: ${warning};
`;

export const CartListPortal: React.FC = () => {
    const { order, totalPrice, toMinimumPrice } = useSelector(({ cartPage }) => cartPage);

    const itemList = React.useMemo(
        () => order.map((itemId, index) => <CartListItemPortal key={itemId} index={index + 1} id={itemId} />),
        [order],
    );

    return (
        <CartListContainerPortal>
            {order.length > 0 && <CartListScrollerPortal itemCount={order.length}>{itemList}</CartListScrollerPortal>}
            <CartTotalPricePortal>Сумма заказа {totalPrice} ₽</CartTotalPricePortal>
            {Boolean(toMinimumPrice) && (
                <StyledMinimalPrice>До минимального заказа еще {toMinimumPrice} ₽</StyledMinimalPrice>
            )}
        </CartListContainerPortal>
    );
};
