import React from 'react';
import { useSelector } from 'hooks/useSelector';
import {
    CartListContainerPortal,
    CartListScrollerPortal,
    CartTotalPricePortal,
} from 'components/Cart/List/CartList@portal';

import { StatusDetailsListItemPortal } from '../ListItem/StatusDetailsListItem@portal';

export const StatusDetailsListPortal: React.FC = () => {
    const { order, totalPrice } = useSelector(({ orderStatusDetailsPage }) => orderStatusDetailsPage);

    const itemList = React.useMemo(
        () =>
            order.map((itemId, index, list) => (
                <StatusDetailsListItemPortal
                    key={itemId}
                    canFocus={index > 0 && index < list.length - 2}
                    index={index + 1}
                    id={itemId}
                />
            )),
        [order],
    );

    return (
        <CartListContainerPortal>
            {order.length > 0 && <CartListScrollerPortal itemCount={order.length}>{itemList}</CartListScrollerPortal>}
            <CartTotalPricePortal>Сумма заказа {totalPrice} ₽</CartTotalPricePortal>
        </CartListContainerPortal>
    );
};
