import React from 'react';

interface UseScrollProgressParams {
    itemCount: number;
    scrollSize?: number;
}

export const useScrollProgress = <C extends HTMLElement = HTMLDivElement>({
    itemCount,
    scrollSize = 1,
}: UseScrollProgressParams) => {
    const [scrollProgress, setScrollProgress] = React.useState(0);

    const scrollerRef = React.useRef<C>(null);

    const handleScroll = React.useCallback(
        () =>
            scrollerRef.current &&
            setScrollProgress(
                scrollerRef.current.scrollTop / (scrollerRef.current.scrollHeight - scrollerRef.current.clientHeight),
            ),
        [setScrollProgress],
    );

    React.useEffect(() => {
        handleScroll(); // пересчитываем все при изменении itemCount
    }, [handleScroll, itemCount]);

    const isScrollable = itemCount > scrollSize;

    return {
        scrollProgress,
        handleScroll,
        scrollerRef,
        isScrollable,
    };
};
